.Header {
  background-image: linear-gradient(transparent, #2f295c), url('cosmic.webp');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 500px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.title {
  font-size: 60px;
  color: white;
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.star-image {
  padding-bottom: 0px;
}

.nav-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
  padding-top: 100px;
}

.home-button {
  text-decoration: none;
  color: white;
  padding-right: 30px;
  padding-left: 10px;
}
.home-button:hover {
  color: white;
}

#dropdown-basic-button {
  color: white;
  font-weight: bold;
  font-size: 24px;
  background-color: transparent;
  border-color: transparent;
}

#dropdown-basic-button:hover {
  color: white;
}

@media only screen and (max-width: 950px) {
  .Header {
    height: 300px;
  }

  .star-image {
    height: 100px;
  }

  .title {
    font-size: 30px;
    width: 200px;
  }

  .nav-buttons {
    display: flex;
    flex-direction: column;
  }

  #dropdown-basic-button {
    color: white;
    font-weight: bold;
    font-size: 20px;
    background-color: transparent;
    border-color: transparent;
  }

  .home-button {
    font-size: 20px;
    padding-right: 5px;
  }
}
